/* -------------------------------------------------------------
 * Forms
 * ---------------------------------------------------------- */
.form {
  margin-bottom: 20px; }
  .form fieldset {
    border: 0; }
  .form legend {
    margin-bottom: 5px; }
  .form label,
  .form .faux-label {
    float: left;
    padding: 0 0 5px 0;
    width: 25%;
    box-sizing: border-box; }
  .form input[type="text"],
  .form input[type="number"],
  .form input[type="email"],
  .form input[type="password"],
  .form input[type="search"],
  .form input[type="file"],
  .form input[type="url"],
  .form select,
  .form textarea {
    padding: 0.3em 0.4em;
    margin: 0 0 5px 5%;
    width: 70%;
    box-sizing: border-box;
    background: #FFFFFF;
    border: solid 1px #CCCCCC;
    color: #000000;
    font-size: 100%;
    font-family: Arial,Verdana; }
  .form textarea,
  .form select[multiple="multiple"] {
    height: 80px; }
  .form .error-msg {
    display: none;
    box-sizing: border-box;
    color: red;
    font-size: 90%; }
  .form .help-text {
    display: block;
    width: 70%;
    margin-left: 30%; }
  .form .buttons {
    padding-left: 30%; }
  .form .field {
    padding-bottom: 10px; }
    .form .field:before, .form .field:after {
      content: " ";
      display: table; }
    .form .field:after {
      clear: both; }
    .form .field.error input[type="text"],
    .form .field.error input[type="number"],
    .form .field.error input[type="email"],
    .form .field.error input[type="password"],
    .form .field.error input[type="search"],
    .form .field.error input[type="file"],
    .form .field.error input[type="url"],
    .form .field.error select,
    .form .field.error textarea {
      border-color: red; }
    .form .field.error .error-msg {
      display: block; }
    .form .field.radiocheck {
      position: relative; }
      .form .field.radiocheck label {
        position: relative;
        width: 100%;
        padding-left: 30px; }
      .form .field.radiocheck input[type="checkbox"],
      .form .field.radiocheck input[type="radio"] {
        position: absolute;
        top: 3px;
        left: 5px; }
      .form .field.radiocheck .error-msg {
        padding-left: 30px;
        clear: left; }
      .form .field.radiocheck.horizontal label {
        float: left;
        width: auto;
        margin-right: 1em; }
    .form .field.required label:after {
      content: " *"; }

