.btn {
  display: inline-block;
  padding: 0 0.5em;
  line-height: 1.7em;
  margin: 0;
  box-sizing: border-box;
  border: solid 1px #AAA;
  border: 0;
  background: #ccc;
  color: #666;
  outline: 0;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  font-size: 1em;
  vertical-align: middle;
  font-family: Arial,Helvetica;
  border-collapse: separate;
  overflow: visible; }
  .btn.btn-block {
    display: block;
    width: 100%;
    text-align: left; }
    .btn.btn-block.center {
      text-align: center; }
  .btn.btn-small {
    font-size: 0.9em;
    line-height: 1.41667em;
    font-weight: normal; }
  .btn.btn-large {
    font-size: 1.4em; }
  .btn:hover, .btn:focus {
    text-decoration: none; }
  .btn:active {
    text-decoration: none; }
  .btn[disabled], .btn.disabled {
    cursor: default;
    text-decoration: line-through; }
  .btn.btn-primary {
    color: #428bca; }
  .btn.btn-secondary {
    color: #428bca; }
  .btn.btn-info {
    color: #5bc0de; }
  .btn.btn-warning {
    color: #f0ad4e; }
  .btn.btn-danger {
    color: #d9534f; }

/* -------------------------------------------------------------
 * Button groups
 * ---------------------------------------------------------- */
.btn-group {
  display: inline-block;
  position: relative;
  vertical-align: middle; }
  .btn-group:before, .btn-group:after {
    content: " ";
    display: table; }
  .btn-group:after {
    clear: both; }
  .btn-group .dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    min-width: 100%;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.5s;
    box-sizing: border-box;
    border-radius: 0 0 2px 2px;
    background: #FFFFFF;
    border: solid 1px silver;
    border-top: 0; }
    .btn-group .dropdown-menu a {
      display: block;
      padding: 0.2em 0.5em; }
  .btn-group.open .dropdown-menu {
    visibility: visible;
    opacity: 1; }
  .btn-group .btn {
    display: block;
    float: left;
    border-radius: 0; }
    .btn-group .btn:first-child {
      border-radius: 2px 0 0 2px; }
    .btn-group .btn + .btn {
      border-radius: 0; }
    .btn-group .btn:last-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0; }
    .btn-group .btn.dropdown-toggle {
      border-top-right-radius: 2px;
      border-bottom-right-radius: 2px; }
    .btn-group .btn + .dropdown-toggle {
      padding-left: 4px;
      padding-right: 4px;
      border-left: 0; }
    .btn-group .btn .caret {
      position: relative;
      display: inline-block;
      width: 15px;
      height: 1em; }
      .btn-group .btn .caret:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        margin: 0 0 0 -5px;
        border-style: solid;
        border-width: 5px 5px 0 5px;
        border-color: #000000 transparent; }

/* -------------------------------------------------------------
 * Lists
 * ---------------------------------------------------------- */
ul {
  margin: 0;
  padding: 0; }
  ul li {
    list-style-type: none; }

ul.list-standard {
  margin: 0 0 1em 1.5em; }
  ul.list-standard li {
    padding-bottom: 0.5em;
    list-style: disc; }

ol.list-standard {
  margin: 0 0 1em 1.5em; }
  ol.list-standard li {
    padding-bottom: 0.5em;
    list-style: decimal; }

dl.list-standard {
  margin-bottom: 0.5em; }
  dl.list-standard dt {
    font-weight: bold; }
  dl.list-standard dd {
    padding: 0 0 0.5em 1em; }

/* -------------------------------------------------------------
 * Media Objects
 * img, object, iframe, etc.
 * ---------------------------------------------------------- */
img,
object {
  max-width: 100%; }

a img {
  border: 0; }

/* -------------------------------------------------------------
 * Useful styles to ensure youtube videos always maintain sensible
 * aspect ratio, wrap iframe in this, set width to 100% and no
 * height attribute
 * ---------------------------------------------------------- */
.videomagic {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
  margin: 30px 0; }
  .videomagic iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

#map-canvas img {
  max-width: inherit; }

/* -------------------------------------------------------------
 * Tables
 * ---------------------------------------------------------- */
.table {
  width: 100%;
  margin: 0 0 0.5em 0;
  border-collapse: collapse; }
  .table th,
  .table td {
    padding: 0.3em 1em;
    vertical-align: top;
    text-align: left; }
  .table tr th:first-child,
  .table tr td:first-child {
    padding-left: 0; }
  .table tr th:last-child,
  .table tr td:last-child {
    padding-right: 0; }

