/* -------------------------------------------------------------
 * Alerts
 * ---------------------------------------------------------- */
.alert {
  position: relative;
  margin: 0 0 0.5em 0;
  padding: 10px 20px;
  background: #a6c8e6; }
  .alert .close {
    position: absolute;
    top: 8px;
    right: 8px;
    text-decoration: none;
    font-weight: bold;
    color: #FFFFFF; }
    .alert .close:hover {
      color: #000000; }
  .alert.alert-warning {
    background: #fae3c4; }
  .alert.alert-error {
    background: #f0b9b8; }
  .alert.alert-success {
    background: #b5dfb5; }

.no-js .alert .close {
  display: none; }

/* -------------------------------------------------------------
 * Popups
 * ---------------------------------------------------------- */
.popupwrapper .mask {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3); }

.popupwrapper .popup {
  display: none;
  position: absolute;
  left: 50%;
  width: 600px;
  padding: 10px;
  margin: 0 0 -300px;
  box-sizing: border-box;
  background: #fff; }
  .popupwrapper .popup.fixed {
    position: fixed;
    top: 10%;
    height: 80%; }
    .popupwrapper .popup.fixed .inner {
      max-height: 90%;
      overflow-y: scroll; }

/* -------------------------------------------------------------
 * Popups - Responsive
 * ---------------------------------------------------------- */
@media (max-width: 767px) {
  .popupwrapper .popup {
    width: 90%;
    margin-left: -45%; } }

/* -------------------------------------------------------------
 * Tabs
 * ---------------------------------------------------------- */
.tabwrapper {
  margin: 0 0 1em 0; }
  .tabwrapper > .nav > li {
    float: left; }
    .tabwrapper > .nav > li > a {
      display: block;
      padding: 0.2em 0.5em;
      text-decoration: none;
      background: #aaa; }
    .tabwrapper > .nav > li.active > a {
      font-weight: bold;
      background: #ccc; }
  .tabwrapper .tabpane {
    display: none;
    padding: 20px;
    clear: both;
    background: #ccc; }
    .tabwrapper .tabpane.active {
      display: block; }
  .tabwrapper.tabs-left:before, .tabwrapper.tabs-left:after {
    content: " ";
    display: table; }
  .tabwrapper.tabs-left:after {
    clear: both; }
  .tabwrapper.tabs-left > .nav {
    float: left;
    width: 20%;
    box-sizing: border-box; }
    .tabwrapper.tabs-left > .nav > li {
      float: none; }
  .tabwrapper.tabs-left .tabpane {
    float: left;
    width: 80%;
    clear: none;
    box-sizing: border-box; }
  .tabwrapper.tabs-right:before, .tabwrapper.tabs-right:after {
    content: " ";
    display: table; }
  .tabwrapper.tabs-right:after {
    clear: both; }
  .tabwrapper.tabs-right > .nav {
    float: right;
    width: 20%;
    box-sizing: border-box; }
    .tabwrapper.tabs-right > .nav > li {
      float: none; }
  .tabwrapper.tabs-right .tabpane {
    float: left;
    width: 80%;
    clear: none;
    box-sizing: border-box; }

/* -------------------------------------------------------------
 * Tabs - responsive
 * ---------------------------------------------------------- */
@media (max-width: 767px) {
  .tabwrapper > .nav > li {
    float: none; }
  .tabwrapper.tabs-left > .nav, .tabwrapper.tabs-right > .nav {
    float: none;
    width: auto; }
  .tabwrapper.tabs-left .tabpane, .tabwrapper.tabs-right .tabpane {
    float: none;
    width: auto; } }

/* -------------------------------------------------------------
 * Wells
 * ---------------------------------------------------------- */
.well {
  padding: 20px;
  margin: 0 0 2em 0;
  box-sizing: border-box; }
